body {
  --active-state-color: #00acdc;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
}

.header__content {
  justify-content: space-between;
  align-items: center;
  width: 1100px;
  margin: 0 auto;
  display: flex;
}

.logo-link {
  width: 40px;
  height: 40px;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  margin-right: 20px;
}

.nav__item:last-child {
  margin-right: 0;
}

.nav__link {
  color: #000;
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  display: flex;
}

.nav__link:hover {
  color: var(--active-state-color);
}

.is-active {
  color: var(--active-state-color);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--active-state-color);
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

@media (width <= 1200px) {
  .header {
    padding: 0 50px;
  }

  .header__content {
    width: 100%;
  }
}
